import { Injectable } from '@angular/core';

import { Http, Headers, RequestOptions } from '@angular/http';
import 'rxjs/add/operator/map';
import 'rxjs/add/observable/of';

@Injectable()
export class ProductService {
	// API_URL = 'http://172.24.62.104/link.eddmi.com/index.php/takeaway/';
	// ENTITY_ID = 2;
	API_URL = 'https://app.linkya.xyz/takeaway/';
	ENTITY_ID = 281;
	fixedMenu: any;
	dailyMenu: any;
	constructor(public http: Http) {
		// console.log('Hello ProductService Provider');
	}

	getFixed(local: string) : Promise<any> {
		return new Promise((resolve, reject) => {
			var url = this.API_URL + 'product/menu?type=fixed&local=' + local;
			let _headers = new Headers({ 'Authorization': this.ENTITY_ID });
			let _options = new RequestOptions({headers: _headers});
			this.http.get(url, _options)
				.map(res => res.json())
				.subscribe(data => {
					this.fixedMenu = data.data.menu;
					resolve(this.fixedMenu);
				}, error => {
					// console.log(error);
					reject(error);
				});
		});
	}  

	getDaily(local: string) : Promise<any> {
		return new Promise((resolve, reject) => {
			var url = this.API_URL + 'product/menu?type=daily&local=' + local;
			let _headers = new Headers({ 'Authorization': this.ENTITY_ID });
			let _options = new RequestOptions({headers: _headers});
			this.http.get(url, _options)
				.map(res => res.json())
				.subscribe(data => {
					this.dailyMenu = data.data.menu;
					resolve(this.dailyMenu);
				}, error => {
					// console.log(error);
					reject(error);
				});
		});
	}
	
}


